/* eslint-disable jsx-a11y/control-has-associated-label */
import React, { useState, useEffect, useContext } from "react";
import PropTypes from "prop-types";
import usePrevious from "../../../hooks/usePrevious";
import "./index.scss";
import { sendData, p2pMessageActions } from "../../../utils/skylink";
import { WidgetContext } from "../../../utils/context";

const ImageRotator = ({ items, productId, mediaIndex }) => {
  const { room } = useContext(WidgetContext);
  const [activeImage, setActiveImage] = useState(mediaIndex);
  const prevItemsLength = usePrevious(items?.length || 0);

  useEffect(() => {
    if (prevItemsLength !== items?.length) {
      setActiveImage(0);
    }
  });

  const onChangeImageIndex = (newIndex) => {
    setActiveImage(newIndex);
    if (room) {
      const { type, methods } = p2pMessageActions.userAction;
      sendData({
        room: room.id,
        type,
        method: methods.productView,
        data: {
          product_id: productId,
          media_index: newIndex,
        },
      });
    }
  };

  useEffect(() => {
    setActiveImage(mediaIndex);
  }, [mediaIndex]);

  return (
    <div className="vclerk-i-r-wrapper">
      <div className="vclerk-i-r-image">
        <img src={`${items[activeImage].url}`} alt="item" />
      </div>
      <div className="vclerk-i-r-dots-container">
        {items.length &&
          items.map((item, index) => (
            <div
              role="button"
              tabIndex={0}
              // eslint-disable-next-line react/no-array-index-key
              key={`image-dot-${index}`}
              onClick={() => onChangeImageIndex(index)}
              className={`vclerk-i-r-dot ${
                activeImage === index ? "vclerk-active-dot" : ""
              }`}
            />
          ))}
      </div>
    </div>
  );
};

ImageRotator.propTypes = {
  items: PropTypes.arrayOf(PropTypes.object).isRequired,
  productId: PropTypes.string.isRequired,
  mediaIndex: PropTypes.number.isRequired,
};

export default ImageRotator;
