import React from "react";
import PropTypes from "prop-types";
import "./index.scss";

const Button = ({ onClick, tabIndex, children }) => (
  <div
    className="vclerk-wizard-button"
    onClick={onClick}
    role="button"
    tabIndex={tabIndex}
  >
    {children}
  </div>
);

Button.defaultProps = {
  tabIndex: 0,
};

Button.propTypes = {
  tabIndex: PropTypes.number,
  children: PropTypes.oneOfType([
    PropTypes.node,
    PropTypes.arrayOf(PropTypes.node),
  ]).isRequired,
  onClick: PropTypes.func.isRequired,
};

export default Button;
