import React, { useState } from "react";
import PropTypes from "prop-types";
import Close from "../../../assets/icons/close";
import VClerkLogo from "../../../assets/icons/VClerkLogo";
import Checkbox from "../../components/Checkbox";
import Button from "../../components/Button";
import "./index.scss";

const Intro = ({ onConfirm, onClose }) => {
  const [hasConfirmedGDPR, setHasConfirmedGDPR] = useState(undefined);

  return (
    <div className="vclerk-widget-intro">
      <div
        role="button"
        className="vclerk-widget-intro-close"
        onClick={onClose}
        tabIndex={0}
      >
        <Close color="#f8f8f8" />
      </div>
      <VClerkLogo color="#beaeff" width={180} height={92} />
      <div className="vclerk-widget-intro-heading">
        Det er bedre å handle på net med VClerk!
      </div>
      <div className="vclerk-widget-intro-description">
        En video ekspeditør er klar til å hjelpe deg å velge riktig produkt. Få
        samme hjelp og service som om du var i butikken - uten reise dit
      </div>
      <div className="vclerk-widget-intro-confirm-wrapper">
        <Checkbox
          onChange={(e) => {
            setHasConfirmedGDPR(e);
          }}
          hasError={
            typeof hasConfirmedGDPR !== "undefined" && !hasConfirmedGDPR
          }
          id="gdpr-confirm"
          label="User agreement and licensing."
        />
      </div>
      <Button
        onClick={() => {
          if (!hasConfirmedGDPR) return hasConfirmedGDPR;
          return onConfirm();
        }}
      >
        Start VClerk
      </Button>
    </div>
  );
};

Intro.propTypes = {
  onConfirm: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired,
};

export default Intro;
