import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import ImageRotator from "../ImageRotator";
import Button from "../Button";
import { trimString } from "../../../utils/stringModifiers";
import "./index.scss";

const getActiveProduct = (prods, aProdID) =>
  prods.find((p) => p.id === aProdID);

const Product = ({ onProductAdd, products, activeProductId, mediaIndex }) => {
  const [state, setState] = useState({
    activeProduct: getActiveProduct(products, activeProductId),
  });

  useEffect(() => {
    const prod = getActiveProduct(products, activeProductId);
    setState({
      activeProduct: prod,
    });
  }, [activeProductId]);

  return (
    <div className="vclerk-p-wrapper">
      {!!state.activeProduct && (
        <>
          <div className="vclerk-p-image-wrapper">
            <ImageRotator
              items={state.activeProduct.additional_image_links}
              productId={state.activeProduct.id}
              mediaIndex={mediaIndex}
            />
          </div>

          <div className="vclerk-p-descr-wrapper">
            <div className="vclerk-p-name">{state.activeProduct.title}</div>
            <div className="vclerk-p-price">
              {`${state.activeProduct.price} ${state.activeProduct.currency}`}
            </div>
            <div className="vclerk-p-description">
              {trimString(state.activeProduct.description, 260)}
            </div>
          </div>
        </>
      )}
      <div className="vclerk-p-action-container">
        <div className="vclerk-p-action-descr">
          <div className="vclerk-p-action-descr-name">
            {state.activeProduct.title}
          </div>
          <div className="vclerk-p-action-descr-price">
            {`${state.activeProduct.price} ${state.activeProduct.currency}`}
          </div>
        </div>
        <Button onClick={() => onProductAdd(state.activeProduct)}>
          Add to Cart
        </Button>
      </div>
      {!state.activeProduct && (
        <div className="wizard-no-selected-product">
          Please select a product to view details.
        </div>
      )}
    </div>
  );
};

Product.defaultProps = {
  onProductAdd: () => {},
  products: [],
  activeProductId: undefined,
  mediaIndex: 0,
};

Product.propTypes = {
  onProductAdd: PropTypes.func,
  products: PropTypes.arrayOf(PropTypes.object),
  activeProductId: PropTypes.string,
  mediaIndex: PropTypes.number,
};

export default React.memo(Product);
