class ErrorMessages {
  constructor(type) {
    this.type = type;
  }

  render(key) {
    switch (this.type) {
      case "missingKey":
        throw new Error(`Missing "${key}" from your model.`);
      case "unknownKey":
        throw new Error(`Key "${key}" does not exist in the schema.`);
      case "invalidType":
        throw new Error(`Key "${key}" has invalid type.`);
      default:
        throw new Error("An error occurred.");
    }
  }
}

export default ErrorMessages;
