import React, { useContext, useEffect } from "react";
import PropTypes from "prop-types";
import skylink from "../../../utils/skylink";
import { WidgetContext } from "../../../utils/context";
import MicrophoneOn from "../../../assets/icons/MicrophoneOn";
import MicrophoneOff from "../../../assets/icons/MicrophoneOff";
import CameraOn from "../../../assets/icons/CameraOn";
import CameraOff from "../../../assets/icons/CameraOff";
import "./index.scss";

const SelfVideo = ({
  style,
  wrapperStyle,
  hideName,
  isFromCustomer,
  streams,
  iconSizes,
  iconTextSize,
}) => {
  const { room, streamMuted, setStreamMuted } = useContext(WidgetContext);

  useEffect(() => {
    streams.forEach((detail) => {
      const { stream, isSelf, isAudio } = detail;
      if (!isSelf) return;
      if (!isAudio) {
        const selfVideo = document.getElementById("selfVideo");
        window.attachMediaStream(selfVideo, stream);
      }
    });
  }, [streams]);

  const mute = ({ audio, video }) => {
    const audioMuted = audio === undefined ? streamMuted?.audio : audio;
    const videoMuted = video === undefined ? streamMuted?.video : video;
    setStreamMuted({ audio: audioMuted, video: videoMuted });
    skylink.muteStreams(room.id, { audioMuted, videoMuted });
  };

  return (
    <div
      className={`vclerk-sv-wrapper ${isFromCustomer ? "vclerk-fc" : ""}`}
      style={wrapperStyle}
    >
      <div className="vclerk-sv-container" style={style}>
        <video className="vclerk-sv" id="selfVideo" autoPlay muted />
      </div>
      {!hideName && <div className="vclerk-sv-video-text">Dette er deg</div>}
      <div className="vclerk-sv-mute-container">
        {streamMuted?.audio ? (
          <div
            className="vclerk-icon-container vclerk-icon-off"
            tabIndex={0}
            role="button"
            aria-label="Unnmute"
            style={{ ...(iconTextSize && { fontSize: iconTextSize }) }}
            onClick={() => mute({ audio: false })}
          >
            <MicrophoneOff width={iconSizes.width} height={iconSizes.height} />
            Off
          </div>
        ) : (
          <div
            className="vclerk-icon-container vclerk-icon-on"
            tabIndex={0}
            role="button"
            aria-label="Mute"
            style={{ ...(iconTextSize && { fontSize: iconTextSize }) }}
            onClick={() => mute({ audio: true })}
          >
            <MicrophoneOn
              color="#080a1e"
              width={iconSizes.width}
              height={iconSizes.height}
            />
            On
          </div>
        )}
        {streamMuted?.video ? (
          <div
            className="vclerk-icon-container vclerk-icon-off"
            tabIndex={0}
            role="button"
            aria-label="Show camera"
            onClick={() => mute({ video: false })}
            style={{ ...(iconTextSize && { fontSize: iconTextSize }) }}
          >
            <CameraOff width={iconSizes.width} height={iconSizes.height} />
            Off
          </div>
        ) : (
          <div
            className="vclerk-icon-container vclerk-icon-on"
            tabIndex={0}
            role="button"
            aria-label="Hide camera"
            style={{ ...(iconTextSize && { fontSize: iconTextSize }) }}
            onClick={() => mute({ video: true })}
          >
            <CameraOn
              color="#080a1e"
              width={iconSizes.width}
              height={iconSizes.height}
            />
            On
          </div>
        )}
      </div>
    </div>
  );
};

const shouldUpdate = (prevState, nextState) => {
  return prevState.id !== nextState.id;
};

SelfVideo.defaultProps = {
  style: {},
  hideName: false,
  wrapperStyle: {},
  isFromCustomer: false,
  iconSizes: {
    width: 24,
    height: 24,
  },
  iconTextSize: 14,
};

SelfVideo.propTypes = {
  style: PropTypes.shape({}),
  wrapperStyle: PropTypes.shape({}),
  hideName: PropTypes.bool,
  isFromCustomer: PropTypes.bool,
  iconSizes: PropTypes.shape({
    width: PropTypes.number,
    height: PropTypes.number,
  }),
  iconTextSize: PropTypes.number,
  streams: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
};

export default React.memo(SelfVideo, shouldUpdate);
