import React, { useContext } from "react";
import PropTypes from "prop-types";
import { sendData, p2pMessageActions } from "../../../utils/skylink";
import "./index.scss";

import { WidgetContext } from "../../../utils/context";

const ProductList = ({ products, activeProductId }) => {
  const { room } = useContext(WidgetContext);

  const clickedProduct = (product) => {
    if (room?.id) {
      const { type, methods } = p2pMessageActions.userAction;
      sendData({
        room: room.id,
        type,
        method: methods.productView,
        data: {
          product_id: product.id,
          media_index: 0,
        },
      });
    }
  };

  return (
    <div className="vclerk-pl-wrapper">
      {products &&
        products.map((p) => (
          <div
            className={`vclerk-pl-row ${
              p.id === activeProductId ? "active-product" : ""
            }`}
            key={p.id}
            role="button"
            tabIndex={0}
            onClick={() => clickedProduct(p)}
          >
            <div className="vclerk-p-col img-container">
              <img
                className="wizard-product-image"
                src={p.image_link}
                alt="product"
              />
            </div>
            <div className="vclerk-p-col p-description">{p.title}</div>
            <div className="vclerk-p-col p-price">
              {`${p.currency} ${p.price}`}
            </div>
          </div>
        ))}
    </div>
  );
};

ProductList.defaultProps = {
  products: [],
  activeProductId: undefined,
};

ProductList.propTypes = {
  products: PropTypes.arrayOf(PropTypes.object),
  activeProductId: PropTypes.string,
};

export default React.memo(ProductList);
