/* eslint-disable no-undef */
/* eslint-disable react/jsx-props-no-spreading */
import React from "react";
import ReactDOM from "react-dom";
import * as Sentry from "@sentry/browser";
import ProductWidget from "./src/core";
import { setTheme } from "./src/utils/theme";

let IS_MOUNTED = false;

// eslint-disable-next-line import/prefer-default-export
export const init = (selector, config, theme, onClose) => {
  setTheme(theme);
  IS_MOUNTED = true;
  const closeWidget = () => {
    if (IS_MOUNTED) {
      ReactDOM.unmountComponentAtNode(selector);
      onClose();
      IS_MOUNTED = false;
    }
  };

  // init sentry
  Sentry.init({
    dsn: VCLERK_SENTRY_DSN,
    environment: typeof VCLERK_ENV !== "undefined" ? VCLERK_ENV : "PROD",
    release: `v${VCLERK_VERSION}`,
  });

  // eslint-disable-next-line react/jsx-props-no-spreading
  ReactDOM.render(
    <ProductWidget {...config} onClose={closeWidget} />,
    selector
  );
};
