import Skylink from "skylinkjs";
import p2pMessageActions from "./p2pMessageActions";

const skylinkInstance = new Skylink({
  // eslint-disable-next-line no-undef
  appKey: VCLERK_SKYLINK_KEY,
});

export default skylinkInstance;

export function sendData({ room, type, method, data }) {
  skylinkInstance.sendP2PMessage(
    room,
    JSON.stringify({
      type,
      method,
      ...data,
    })
  );
}

export { p2pMessageActions };
