/* eslint-disable jsx-a11y/media-has-caption */
import React, { useEffect, useContext, useState, useRef } from "react";
import { SkylinkEventManager, SkylinkConstants } from "skylinkjs";
import PropTypes from "prop-types";
import { WidgetContext } from "../../../utils/context";
import states from "../../../utils/states";
import Spinner from "../Spinner";
import "./index.scss";

const PeerVideo = ({ streams, onPeerLeft }) => {
  const { setWidgetState } = useContext(WidgetContext);
  const [peerLoading, setPeerLoading] = useState(true);
  const isPeerPresent = useRef();
  const [peerName, setPeerName] = useState();

  useEffect(() => {
    streams.forEach((detail) => {
      const { stream, isSelf, isAudio, peerInfo } = detail;
      if (isSelf) return;
      setPeerName(peerInfo.userData.displayName || "Vi");
      setWidgetState(states.connected);
      setPeerLoading(false);
      isPeerPresent.current = true;
      if (isAudio) {
        const peerAudio = document.getElementById("peerAudio");
        window.attachMediaStream(peerAudio, stream);
      } else {
        const peerVideo = document.getElementById("peerVideo");
        window.attachMediaStream(peerVideo, stream);
        setPeerLoading(false);
      }
    });
  }, [streams]);

  useEffect(() => {
    const peerLeft = () => {
      if (isPeerPresent.current) {
        isPeerPresent.current = false;
        const peerVideo = document.getElementById("peerVideo");
        window.attachMediaStream(peerVideo, null);
        setWidgetState(states.init);
        onPeerLeft();
      }
    };

    SkylinkEventManager.addEventListener(
      SkylinkConstants.EVENTS.PEER_LEFT,
      peerLeft
    );
    SkylinkEventManager.addEventListener(
      SkylinkConstants.EVENTS.SESSION_DISCONNECT,
      peerLeft
    );
    SkylinkEventManager.addEventListener(
      SkylinkConstants.EVENTS.STREAM_ENDED,
      peerLeft
    );

    return () => {
      SkylinkEventManager.removeEventListener(
        SkylinkConstants.EVENTS.PEER_LEFT,
        peerLeft
      );
      SkylinkEventManager.removeEventListener(
        SkylinkConstants.EVENTS.SESSION_DISCONNECT,
        peerLeft
      );
      SkylinkEventManager.removeEventListener(
        SkylinkConstants.EVENTS.STREAM_ENDED,
        peerLeft
      );
    };
  }, []);

  return (
    <div className="vclerk-pv-wrapper">
      <div
        className={`vclerk-pv-spinner ${
          peerLoading ? "vclerk-show" : "vclerk-hide"
        }`}
      >
        <Spinner />
      </div>
      <div
        className={`vclerk-pv-container ${
          peerLoading ? "vclerk-hide" : "vclerk-show"
        }`}
      >
        <video className="vclerk-pv" id="peerVideo" autoPlay />
      </div>
      <div className="vclerk-pv-text">{`${peerName} hjelper deg`}</div>
      <audio id="peerAudio" autoPlay />
    </div>
  );
};

PeerVideo.defaultProps = {
  onPeerLeft: () => {},
};

PeerVideo.propTypes = {
  onPeerLeft: PropTypes.func,
  streams: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
};

export default React.memo(PeerVideo);
