import React, { useState, useEffect, useContext } from "react";
import { SkylinkConstants, SkylinkEventManager } from "skylinkjs";
import PropTypes from "prop-types";
import PeerVideo from "../../components/PeerVideo";
import ProductList from "../../components/ProductList";
import Product from "../../components/Product";
import skylink, { p2pMessageActions, sendData } from "../../../utils/skylink";
import useEventListener from "../../../hooks/useEventListener";
import { WidgetContext } from "../../../utils/context";
import states from "../../../utils/states";
import usePrevious from "../../../hooks/usePrevious";
import VClerkLogo from "../../../assets/icons/VClerkLogo";
import Button from "../../components/Button";
import SelfVideo from "../../components/SelfVideo";
import Hangup from "../../../assets/icons/Hangup";
import "./index.scss";
import CloseIcon from "../../../assets/icons/closeIcon";
import checkSchema from "../../../utils/productSchema";

const Customer = ({ onClose, onProductAdd, queue, apiKey }) => {
  const { room, setRoom, setWidgetState, setStreamMuted } = useContext(
    WidgetContext
  );
  const [loading, setLoading] = useState(true);
  const [streams, setStreams] = useState([]);
  const [products, setProducts] = useState([]);
  const [activeProduct, setActiveProduct] = useState({
    id: null,
    mediaIndex: 0,
  });
  const [closing, setClosing] = useState(false);
  const prevRoom = usePrevious(room);

  const refreshRoom = () => {
    // eslint-disable-next-line no-undef
    fetch(`${VCLERK_ENDPOINT}${queue ? `/${queue}` : ""}/help/${room.id}`, {
      headers: {
        "X-VideoClerk-API-Key": apiKey,
      },
    }).then((response) => {
      response.json().then((json) => {
        if (json.state === "waiting") {
          setRoom({ ...room, ...json });
          setTimeout(() => {
            refreshRoom();
          }, 1000);
        }
      });
    });
  };

  const getRoom = () => {
    // eslint-disable-next-line no-undef
    fetch(`${VCLERK_ENDPOINT}${queue ? `/${queue}` : ""}/help`, {
      headers: {
        "X-VideoClerk-API-Key": apiKey,
      },
    }).then((response) => {
      response.json().then((json) => {
        setRoom(json);
        setWidgetState(states.inQueue);
      });
    });
  };

  const handleProductAddedToCart = (product) => {
    if (room?.id && product) {
      const { type, methods } = p2pMessageActions.userAction;
      sendData({
        room: room.id,
        type,
        method: methods.addToCart,
        data: {
          product_id: product.id,
        },
      });
      onProductAdd(product);
    }
  };

  const onIncomingMessage = (evt) => {
    const { content } = evt.detail.message;
    const json = JSON.parse(content);
    if (!evt.detail.isSelf) {
      if (json.type === p2pMessageActions.init.type) {
        const { methods } = p2pMessageActions.init;
        if (json.method === methods.res) {
          checkSchema(json.products);
          setProducts(json.products);
          setActiveProduct({
            id: json.products[0].id,
            mediaIndex: 0,
          });
          setLoading(false);
        }
      }
    }
    if (json.type === p2pMessageActions.userAction.type) {
      const { methods } = p2pMessageActions.userAction;
      if (json.method === methods.productView) {
        setActiveProduct({
          id: json.product_id,
          mediaIndex: json.media_index,
        });
      }
      if (json.method === methods.productRemove) {
        setProducts((prevProducts) =>
          prevProducts.filter((p) => p.id !== json.product_id)
        );
      }
      if (json.method === methods.productAdd) {
        setProducts((prevProducts) => {
          if (prevProducts.findIndex((p) => p.id === json.product) >= 0) {
            return prevProducts;
          }
          return [...prevProducts, json.product];
        });
      }
    }
  };

  const getInitialProducts = (evt) => {
    if (!!evt.detail.state && evt.detail.state === "open") {
      const { type, methods } = p2pMessageActions.init;
      sendData({
        room: room.id,
        type,
        method: methods.req,
      });
    }
  };

  const onIncomingStream = (evt) => {
    setStreams((prevStreams) => prevStreams.concat(evt.detail));
  };

  useEventListener(
    SkylinkConstants.EVENTS.ON_INCOMING_STREAM,
    onIncomingStream,
    SkylinkEventManager
  );
  useEventListener(
    SkylinkConstants.EVENTS.ON_INCOMING_MESSAGE,
    onIncomingMessage,
    SkylinkEventManager
  );
  useEventListener(
    SkylinkConstants.EVENTS.DATA_CHANNEL_STATE,
    getInitialProducts,
    SkylinkEventManager
  );

  useEffect(() => {
    getRoom();
  }, []);

  useEffect(() => {
    if (prevRoom?.id !== room?.id) {
      refreshRoom(room.id);
      skylink
        .joinRoom({
          audio: true,
          video: true,
          roomName: room.id,
        })
        .then(() => {
          setWidgetState(states.connecting);
        });
    }
  }, [room]);

  if (loading) {
    return (
      <div className="vclerk-wl-container">
        <div className="vclerk-wl-left-wrapper">
          <div className="vclerk-wl-content">
            <VClerkLogo width={140} height={62} color="#beaeff" />
          </div>
          <div className="vclerk-wl-content">
            <div className="vclerk-wl-cp-title">Din posisjon i koen</div>
            <div className="vclerk-wl-cp-count">{room?.position ?? "--"}</div>
          </div>
          <div className="vclerk-wl-content">
            <div className="vclerk-wl-cp-title">Estimert ventetid</div>
            <div className="vclerk-wl-cp-count">
              {`${
                room?.estWaitDuration
                  ? Math.round(room?.estWaitDuration / 60000)
                  : "-- "
              }m`}
            </div>
          </div>
          <div className="vclerk-wl-content">
            <Button onClick={onClose}>Avbryt</Button>
          </div>
        </div>
        <div className="vclerk-wl-right-container">
          <SelfVideo
            iconSizes={{
              width: 42,
              height: 42,
            }}
            streams={streams}
            iconTextSize={24}
            style={{ width: 240, height: 240 }}
          />
        </div>
      </div>
    );
  }

  const handleClosing = (close) => {
    // TODO handle this with statuses
    // ideally we should stop the tracks
    // and restore when you go in and out of ending the conversations
    setStreamMuted({ audio: close, video: close });
    skylink.muteStreams(room.id, {
      audioMuted: close,
      videoMuted: close,
    });
    setClosing(close);
  };

  const handleClose = () => {
    if (room?.id) {
      skylink.leaveRoom(room.id);
    }
    onClose();
  };

  return (
    <div
      className="vclerk-cv-wrapper"
      style={{ ...(closing && { overflowY: "hidden" }) }}
    >
      {closing && (
        <div className="vclerk-end-wrapper">
          <div className="vclerk-end-logo">
            <VClerkLogo width={160} height={72} color="#beaeff" />
          </div>
          <div className="vclerk-end-descr">
            <div className="vclerk-end-title">Tusen takk for handelen!</div>
            <div className="vclerk-end-subtitle">
              Varene dere ligger i handlekurven klar til betaling.
            </div>
          </div>
          <div className="vclerk-end-actions">
            <div>
              <Button onClick={handleClose}>Gå til betaling</Button>
            </div>
            <div>
              <Button onClick={() => handleClosing(!closing)}>
                Fortsett samtalen
              </Button>
            </div>
          </div>
        </div>
      )}
      <div className="vclerk-cv-row">
        <div className="vclerk-cv-videos-wrapper">
          <SelfVideo
            isFromCustomer
            hideName
            streams={streams}
            wrapperStyle={{
              flex: "1 1 0",
              alignSelf: "stretch",
              paddingRight: 12,
            }}
          />
          <PeerVideo onPeerLeft={handleClose} streams={streams} />
        </div>
        <div className="vclerk-cv-pl-wrapper">
          {activeProduct.id && (
            <ProductList
              products={products}
              activeProductId={activeProduct.id}
            />
          )}
        </div>
      </div>
      <div className="vclerk-cv-p-wrapper">
        {activeProduct.id && (
          <Product
            products={products}
            activeProductId={activeProduct.id}
            mediaIndex={activeProduct.mediaIndex}
            onProductAdd={handleProductAddedToCart}
          />
        )}
      </div>
      <div className="vclerk-cv-wrapper-actions">
        <div
          className="vclerk-cv-wrapper-close"
          tabIndex={0}
          role="button"
          onClick={() => handleClosing(!closing)}
        >
          <Hangup width={22} height={22} />
        </div>
        <div className="vclerk-cv-wrapper-logo">
          <VClerkLogo color="#beaeff" width={82} height={28} />
        </div>
      </div>
      <div
        tabIndex={0}
        role="button"
        className="vclerk-cv-close-sm"
        onClick={() => handleClosing(!closing)}
      >
        <CloseIcon />
      </div>
    </div>
  );
};

Customer.defaultProps = {
  onClose: () => {},
  onProductAdd: () => {},
};

Customer.propTypes = {
  onClose: PropTypes.func,
  onProductAdd: PropTypes.func,
  queue: PropTypes.string.isRequired,
  apiKey: PropTypes.string.isRequired,
};

export default Customer;
