// We could use some external library for more explicit checks
// tho decided to try it like this to avoid increasing the dist size
import ErrorMessages from "./errorMessages";

const productSchema = {
  id: {
    type: "string",
    required: true,
  },
  title: {
    type: "string",
    required: true,
  },
  description: {
    type: "string",
    required: true,
  },
  image_link: {
    type: "string",
    required: true,
  },
  additional_image_links: {
    type: "array",
    required: true,
    value: {
      type: "object",
    },
  },
  price: {
    type: "float",
    required: true,
  },
  currency: {
    type: "string",
    required: true,
  },
  external_id: {
    type: "string",
    required: false,
  },
  mpn: {
    type: "string",
    required: false,
  },
};
function isFloatNumber(num) {
  return num === +num && num !== (num || 0);
}
const checkSchema = (products) => {
  if (!products || !products.length) return;
  for (let i = 0; i < products.length; i += 1) {
    // Check if the product has missing required keys
    const entries = Object.entries(productSchema)
      .filter(([, value]) => value.required)
      .map((val) => val[0]);
    const keys = Object.keys(products[i]);
    const filteredKeys = entries.filter((key) => !keys.includes(key));
    if (filteredKeys.length > 0) {
      new ErrorMessages("missingKey").render(filteredKeys.join(", "));
    }
    Object.entries(products[i]).forEach(([key]) => {
      // check if the key exists in the schema
      if (!Object.keys(productSchema).includes(key))
        new ErrorMessages("unknownKey").render(key);
      // check if the key is of the expected types
      // string and float and int
      if (
        (productSchema[key].type === "string" &&
          typeof products[i][key] !== "string") ||
        (products[i][key].type === "float" &&
          !isFloatNumber(products[i][key])) ||
        (products[i][key].type === "int" &&
          !Number.isSafeInteger(products[i][key]))
      ) {
        new ErrorMessages("invalidType").render(key);
      }
      // array of the type
      // TODO improve this, cause it will break for an array of array, number etc
      if (
        productSchema[key].type === "array" &&
        products[i][key].constructor === Array
      ) {
        if (products[i][key].length > 0) {
          for (let j = 0; j < products[i][key].length; j += 1) {
            // eslint-disable-next-line valid-typeof
            if (typeof products[i][key][j] !== productSchema[key].value.type) {
              new ErrorMessages("invalidType").render(key);
            }
          }
        }
      }
    });
  }
};

export default checkSchema;
